.AboutMe {
    background: black;
    color: white;
    padding-bottom: 5%;
}

.AboutMe .Container {
    display: flex;
}

.AboutMe .Container .Text {
    width: 47%;
    margin-left: 3%;
}

.AboutMe .Container .Text h2 {
    font-weight: 900;
}

.AboutMe .Container .Photo {
    display: flex;
    width: 50%;
    height: 50%;
    margin-top: 10vh;
    justify-content: center;

}

.AboutMe .Container .Photo .Me {
    width: 40%;
    max-width: 500px;
    border-radius: 10%;
}

@media (max-width: 500px) {
    .AboutMe .Container
    {
        flex-direction: column;
        align-items: center;
    }
    .AboutMe .Container .Text
    {
        width: 80%;
    }
    .AboutMe .Container .Photo
    {
        width: 70%;
    }
}